import React from "react";
import { InfoRow, LessonDetails, TagsList } from "./HomeGroupLessonMetaInfo.styled";

function HomeGroupLessonMetaInfo({ author, book, chapter, verses, date, duration, theme, tags }) {
  const showLocation = chapter || verses;

  return (
    <>
      <LessonDetails>
        {author && (
          <InfoRow>
            <dt>:</dt>
            <dd>
              <span>{author}</span>
            </dd>
          </InfoRow>
        )}
        {book && (
          <InfoRow>
            <dt>:</dt>
            <dd>
              <span>
                {book} {chapter ? ` ${chapter}` : ""}
              </span>
            </dd>
          </InfoRow>
        )}
        {showLocation && (
          <InfoRow>
            <dt> :</dt>
            <dd>
              <span>{verses ? `${verses}` : ""}</span>
            </dd>
          </InfoRow>
        )}
        {date && (
          <InfoRow>
            <dt>:</dt>
            <dd>
              <span>{date}</span>
            </dd>
          </InfoRow>
        )}
        {duration && (
          <InfoRow>
            <dt>:</dt>
            <dd>
              <span>{duration}</span>
            </dd>
          </InfoRow>
        )}
        {theme && (
          <InfoRow>
            <dt>:</dt>
            <dd>
              <span>{theme}</span>
            </dd>
          </InfoRow>
        )}
      </LessonDetails>
      {tags && tags.length > 0 && (
        <TagsList>
          {tags.map((tag, index) => (
            <li key={index}>{tag}</li>
          ))}
        </TagsList>
      )}
    </>
  );
}

export default HomeGroupLessonMetaInfo;
